<template>
  <ul class="crumbs__list">
    <router-link
      v-for="(item, index) of items"
      :key="index"
      :to="{ name: item.routeName }"
      v-slot="{ navigate, isExactActive }"
      custom
    >
      <li
        class="crumbs__item"
        :class="{
          'crumbs__item--active': isExactActive,
          'crumbs__item--disabled': !item.isAllowed
        }"
        @click="navigate"
      >
        {{ item.text }}
      </li>
    </router-link>
  </ul>
</template>

<script>
import { getterTypes } from "@/store/order";
import { mapGetters } from "vuex";

export default {
  name: "TheCrumbs",
  computed: {
    ...mapGetters({
      isLocationFilled: getterTypes.isLocationFilled,
      isModelFilled: getterTypes.isModelFilled,
      isPeriodFilled: getterTypes.isPeriodFilled
    }),
    items() {
      return [
        {
          text: this.$t("orderPage.crumbs.location"),
          routeName: "orderLocation",
          isAllowed: true
        },
        {
          text: this.$t("orderPage.crumbs.model"),
          routeName: "orderModel",
          isAllowed: this.isLocationFilled
        },
        {
          text: this.$t("orderPage.crumbs.additionally"),
          routeName: "orderAdditionally",
          isAllowed: this.isModelFilled
        },
        {
          text: this.$t("orderPage.crumbs.total"),
          routeName: "orderTotal",
          isAllowed: this.isPeriodFilled
        }
      ];
    },
    routeName() {
      return this.$route.name;
    }
  }
};
</script>

<style scoped lang="scss">
.crumbs__item {
  position: relative;
  display: inline-block;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  transition: all 0.3s;
  color: $black;

  &--active {
    color: $accent;
  }

  &--disabled {
    color: $dark-grey;
    pointer-events: none;
  }

  &:not(:last-child) {
    padding-right: 22px;
    margin-right: 16px;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      height: 8px;
      width: 6px;
      background: url("~@/assets/triangle-right.svg") no-repeat center;
      background-size: contain;
    }
  }
}
</style>
